@import
"mq",
"color-vars",
"settings";

// $nav-width: 12rem;

p,
.text {
  padding: .5em 0;
   @include mq($from: tablet) {
    font-size: 1.25em;
    max-width: 34em;
  }
}

li p {
  @include mq($from: tablet) {
    font-size: 1rem;
    max-width: 100%;
  }
}

// moved with javascript to .innerwrap for large and wide heading ...
.title-post {
  -webkit-font-smoothing: antialiased;
  margin: auto;
  padding-top: 1rem;
  padding-bottom: 8rem;
  text-align: center;
  max-width: 88%;
  line-height: 1;

  @include mq($from: tablet) {
    margin-top: 2rem;
    margin-bottom: 3rem;
  }
}

// font-sizes on smaller screens for heading
.fs-3 {
  font-size: 3rem;
}
.fs-3_5 {
  font-size: 3.5rem;
}
.fs-3_75 {
  font-size: 3.75rem;
}
.fs-4 {
  font-size: 4rem;
}

.page .sup-link,
.page .sup-link:hover {
  text-decoration: none;
  border-bottom: none;
  box-shadow: none;
  padding: .5em;
  margin: 0 -.5em;
}
sup span {
  position: relative;
  font-size: 1.25rem;
  line-height: 1.5;
  color: $base00;
  display: none;
}
.sup-link:hover span {
  display: block;
  padding: 1em;
}

// font-sizes scaling with screen-width for heading
@include mq($from: tablet) {
  .fs-desktop-vw-5 {
    font-size: 5vw;
  }
  .fs-desktop-vw-5_25 {
    font-size: 5.25vw;
  }
  .fs-desktop-vw-5_5 {
    font-size: 5.5vw;
  }
  .fs-desktop-vw-5_75 {
    font-size: 5.75vw;
  }
  .fs-desktop-vw-6_25 {
    font-size: 6.25vw;
  }
  .fs-desktop-vw-6_5 {
    font-size: 6.5vw;
  }
  .fs-desktop-vw-6_75 {
    font-size: 6.75vw;
  }
  .fs-desktop-vw-7 {
    font-size: 7vw;
  }
  .fs-desktop-vw-7_25 {
    font-size: 7.25vw;
  }
  .fs-desktop-vw-7_5 {
    font-size: 7.5vw;
  }
  .fs-desktop-vw-8 {
    font-size: 8vw;
  }
  .fs-desktop-vw-9 {
    font-size: 9vw;
  }
  .fs-desktop-vw-9_5 {
    font-size: 9.5vw;
  }
  .fs-desktop-vw-10 {
    font-size: 10vw;
  }
  .fs-desktop-vw-12 {
    font-size: 12vw;
  }
  .fs-desktop-vw-17_25 {
    font-size: 17.25vw;
  }
}

.fs-desktop-vw-7,
.fs-desktop-vw-7_25,
.fs-desktop-vw-7_5,
.fs-desktop-vw-8,
.fs-desktop-vw-9,
.fs-desktop-vw-9_5,
.fs-desktop-vw-10,
.fs-desktop-vw-12,
.fs-desktop-vw-17_25 {
  @include mq($from: desktop) {
    letter-spacing: -2px;
  }
  @include mq($from: wide) {
    letter-spacing: -3px;
  }
}

.page {
  padding: 1em 2em;
  background-color: #fff;
  max-width: 60em;
  margin: 0 auto;
  line-height: 1.5;

  .theme-dark &{
    line-height: 1.65;
    background-color: $dark01;
    color: $light01;
  }


  @include mq($until: tablet) {
    flex: 1;
    z-index: 1;
  }
}

.no-flexbox .page {
  padding-bottom: 5em;
}

.ui-gutter {
  text-align: left;
}

.example {
  overflow: auto;
  padding: .5em 2em 1em 3em;
  border-left: 10px solid $base05;
  // background-color: $light_red-magenta01;
  background-color: $light03;
  // border-left-width: 10px;
  // border-color: $base06 $light03 $base05 $base05;
  margin: 2em 0 3em;
  .theme-dark & .text {
    color: $dark02;
  }
}

.container-media video {
  border: 1px solid $base06;
  border-radius: 10px;
  box-shadow: 4px 7px 12px rgba($base00,.35);
  padding: .5em;
}
